import React, {Component} from 'react'

class ErrorBoundary extends Component{

    constructor(props){
        super(props)

        this.state = {
            activo: false
        }

    }

    estaActivo = () => {
        return this.state.activo ? "Activo" : "No Activo"
    }

    render(){
        return (
        
        <div>
            <button onClick={ () => {

            }}>Activar</button>

            <h1>    
                ErrorBoundary {this.props.saludo} 
                {
                    this.estaActivo()
                }
            </h1>

        </div>
          
        )
    }

}

export default ErrorBoundary
